export default function jsonToExcel(data, head, fileName = "") {
    let str = head ? head + "\n" : "";
    data.forEach((item) => {
        str = `${str + item.type},`;
        str = `${str + item.content},`;
        if (item.type === "") {
            str = `${str + ""},`;
            str = `${str + ""},`;
            str = `${str},`;
            str = `${str},`;
        } else {
            str = `${str + item.optionA},`;
            str = `${str + item.optionB},`;
            str = `${str + item.optionC},`;
            str = `${str + item.optionD},`;
        }
        str = `${str + item.answer},`;
        str += "\n";
    });
    const url =
        "data:text/csv;charset=utf-8,\ufeff" + encodeURIComponent(str);
    // a
    const link = document.createElement("a");
    link.href = url;
    // 
    link.download = `${fileName + ".csv"}`;
    link.click();
}